* {
	user-select: none;
}
#App {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	height: 100vh;
	background-color: black;
	color: white;
	text-align: center;
}
#download-btn {
	cursor: pointer;
	padding: 10px;
	font-size: 20px;
	margin: 30px;
	background-color: white;
	border-radius: 10px;
	transition: 0.5s;
	border: 2px solid white;
	outline: none;
}
#download-btn:hover,
#download-btn:active {
	background-color: teal;
	border: 2px solid yellow;
	color: white;
}
#header-wrapper {
	display: flex;
	align-items: center;
	gap: 5px;
}
#creds {
	position: absolute;
	left: 10px;
	top: 10px;
	cursor: pointer;
	display: flex;
	gap: 10px;
}
#cred-name {
	color: teal;
}
